.App{
  height: 100vh;
  width: 100%;
  overflow: auto;
  background: rgb(77,77,77);
  display: flex;
  align-content: center;
  justify-content: center;
  align-items: center;
}

.App-page{
  width: 1200px;
  position: relative;
  height: 686px;
  /*border: 1px solid #000;*/
  box-shadow: 1px 0px 7px 2px rgba(0, 0, 0, 0.2)
}

.App-page img{
  width: 100%;
  height: 100%;
}

p.time {
  position: absolute;
  top: 220px;
  left: 78px;
  font-weight: bold;
  font-family: "Gilmer Medium";
}

.battery-text{
  writing-mode: vertical-rl;
  text-orientation: mixed;
  position: absolute;
  top: 211px;
  font-family: "Gilmer Light";
  font-size: 10px;
  transform: rotate(180deg);
  right: 5px;
}
.battery-container{
  display: flex;
  flex-direction: column;
  position: absolute;
  height: 415px;
  top: 254px;
  right: 8px;
  justify-content: flex-end
}

.battery-container img{
  width: 28px;
  height: 30px;
  margin: 6px 0;
}

.balls-container{
  position: absolute;
  left: 45px;
  right: 51px;
  top: 0;
  bottom: 0;
  /*background: rgba(246, 46, 46, 0.26);*/
}

.ball{
  /*background-color: #ffffff !important;*/
  background-size: cover !important;
  cursor: pointer;
  transition: transform 0.3s ease;
  margin-top: -10px;
  opacity: 0;
  animation: fadeIn 1s ease-in-out forwards;
}


@keyframes fadeIn {
  from {
    opacity: 0;
    margin-top: -100px;
  }
  to {
    opacity: 1;
    margin-top: 0;
  }
}

.ball-hover{
  transform: scale(1.2)!important;
  z-index: 10000;
  /*background-color: #ffffff !important;*/
}


